.button {
    width: 132px;
    height: 30px;
    padding: 5px 24px;
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    color: #FFF;
    border-radius: 3px;
    border:none
}

.disabled {
    background-color: #004D99;
    color: #002E5C
}

.secondary {
    color: #06C;
    border: 1px solid #06C;
    background-color: white;

}

.default {
    background-color: rgba(0,102,204,1);
}

.red {
    background-color:rgba(204,20,57,1);
}

.default:hover {
    background-color: #0080FF;;
}

.red:hover {
    background-color: #FF1A47;;
}

.secondary:hover {
    background-color: #E5F0FA;
}


.default:active {
   background-color: #0059B2;
}

.secondary:active {
    background-color: #C9E5FF;
}

.red:active {
    background-color: #B21232;
}

.button:disabled {
    opacity: 0.5;
}
